/* card list */
.h-card-list {
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.product img {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

.h-card-img {
    position: relative;
    width: 220px;
    height: 320px;
    margin: 0 0 1rem 1rem;
    transition: all 1s;
    overflow: hidden;
    border-radius: 25% 0;
}

.h-card-img a img {
    width: 100%;
}

.h-card-img:hover .h-card-details {
    top: 0%;
}

.h-card-img::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: -700px;
    width: 100%;
    height: 100%;
    transition: 0.5s;
    background: linear-gradient(0deg, #410702ad 10%, #00050ad1 80%);
    z-index: 1;
}

.h-card-img:hover::before {
    bottom: 0px;
}

.h-card-details {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 0px 5px;
    color: #fff;
    transition: 0.5s cubic-bezier(0.54, -0.36, 0.43, 1.46);
    transition-delay: 0.2s;
    z-index: 2;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column nowrap;
            flex-flow: column nowrap;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
}

.h-categories-product {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

